@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
/* Global Styles */
:root {
  --primary-color: #1f4068;
  --dark-color: #1b1b2f;
  --light-color: #eef3fa;
  --danger-color: #e43f5a;
  --success-color: #58b4ae;
  --mild-color: #c1d3ed;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  background-color: #eef3fa;
  background-color: var(--light-color);
  color: #1b1b2f;
  color: var(--dark-color);
}

a {
  color: #1f4068;
  color: var(--primary-color);
  text-decoration: none;
  transition: opacity 0.2s ease-in;
}

a:hover {
  opacity: 0.8;
}

ul {
  list-style: none;
  align-items: center;
}

.ul-block {
  display: block;
}

.ul-flex {
  display: flex;
}

img {
  width: 100%;
}

/* Utilities */
.container {
  max-width: 1100px;
  margin: auto;
  overflow: hidden;
  padding: 0 2rem;
}

/* Text Styles*/
.x-large {
  font-size: 4rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.large {
  font-size: 3rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.mid-large {
  font-size: 2rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.lead {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.regular {
  font-size: 1rem;
  margin-bottom: 1rem;
}

.text-center {
  text-align: center;
}

.text-primary {
  color: #1f4068;
  color: var(--primary-color);
}

.text-dark {
  color: #1b1b2f;
  color: var(--dark-color);
}

.text-success {
  color: #58b4ae;
  color: var(--success-color);
}

.text-danger {
  color: #e43f5a;
  color: var(--danger-color);
}

.text-mild {
  color: #c1d3ed;
  color: var(--mild-color);
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

/* Center All */
.all-center {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: auto;
  justify-content: center;
  align-items: center;
  text-align: center;
}

/* Cards */
.card {
  padding: 1rem;
  /* border: #c1d3ed 1px solid; */
  margin: 0.7rem 0;
  transition: background-color 0.2s ease-in;
  background: #dfe8f6;
}

.card:hover {
  background-color: #fdfefe;
  border: none;
}

/* .card:hover {
  background-color: darken(var(--light-color), 10%);
} */

/* List */
.list {
  margin: 0.5rem 0;
}

.list li {
  padding-bottom: 0.3rem;
}

/* Padding */
.p {
  padding: 0.5rem;
}
.p-1 {
  padding: 1rem;
}
.p-2 {
  padding: 2rem;
}
.p-3 {
  padding: 3rem;
}
.py {
  padding: 0.5rem 0;
}
.py-1 {
  padding: 1rem 0;
}
.py-2 {
  padding: 2rem 0;
}
.py-3 {
  padding: 3rem 0;
}

/* Margin */
.m {
  margin: 0.5rem;
}
.m-1 {
  margin: 1rem;
}
.m-2 {
  margin: 2rem;
}
.m-3 {
  margin: 3rem;
}

.mx {
  margin: 0 0.5rem;
}
.mx-1 {
  margin: 0 1rem;
}
.mx-2 {
  margin: 0 2rem;
}
.mx-3 {
  margin: 0 3rem;
}

.mr {
  margin-right: 0.5rem;
}
.mr-1 {
  margin-right: 1rem;
}
.mr-2 {
  margin-right: 2rem;
}
.mr-3 {
  margin-right: 3rem;
}

.my {
  margin: 0.5rem 0;
}
.my-1 {
  margin: 1rem 0;
}
.my-2 {
  margin: 2rem 0;
}
.my-3 {
  margin: 3rem 0;
}

.mt {
  margin-top: 0.5rem;
}
.mt-1 {
  margin-top: 1rem;
}
.mt-2 {
  margin-top: 2rem;
}
.mt-3 {
  margin-top: 3rem;
}

.mb {
  margin-bottom: 0.5rem;
}
.mb-1 {
  margin-bottom: 1rem;
}
.mb-2 {
  margin-bottom: 2rem;
}
.mb-3 {
  margin-bottom: 3rem;
}

/* Grid */
.grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
}

.grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
}

.grid-4 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1rem;
}

.btn {
  display: inline-block;
  background: #eef3fa;
  background: var(--light-color);
  color: #1b1b2f;
  color: var(--dark-color);
  padding: 0.4rem 1.3rem;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  margin-right: 0.5rem;
  transition: opacity 0.2s ease-in;
  outline: none;
  align-items: center;
}

.btn-link {
  background: none;
  padding: 0;
  margin: 0;
}

.btn-block {
  display: block;
  width: 100%;
}

.btn-sm {
  font-size: 0.8rem;
  padding: 0.3rem 1rem;
  margin-right: 0.2rem;
}

.badge {
  display: inline-block;
  font-size: 0.8rem;
  padding: 0.2rem 0.7rem;
  text-align: center;
  margin: 0.3rem;
  background: #eef3fa;
  background: var(--light-color);
  color: #1b1b2f;
  color: var(--dark-color);
  border-radius: 15px;
}

.alert {
  padding: 0.7rem;
  margin: 0 0;
  opacity: 0.9;
  background: none;
  color: #1b1b2f;
  color: var(--dark-color);
}

.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
  background: #1f4068;
  background: var(--primary-color);
  color: #eef3fa;
  color: var(--light-color);
}

.btn-light,
.bg-light,
.badge-light,
.alert-light {
  background: #eef3fa;
  background: var(--light-color);
  color: #1b1b2f;
  color: var(--dark-color);
}

.btn-dark,
.bg-dark,
.badge-dark,
.alert-dark {
  background: #1b1b2f;
  background: var(--dark-color);
  color: #eef3fa;
  color: var(--light-color);
}

.btn-danger,
.bg-danger,
.badge-danger,
.alert-danger {
  background: #e43f5a;
  background: var(--danger-color);
  color: #eef3fa;
  color: var(--light-color);
}

.btn-success,
.bg-success,
.badge-success,
.alert-success {
  background: #58b4ae;
  background: var(--success-color);
  color: #eef3fa;
  color: var(--light-color);
}

.btn-mild,
.bg-mild,
.badge-mild,
.alert-mild {
  background: #c1d3ed;
  background: var(--mild-color);
  color: #1b1b2f;
  color: var(--dark-color);
}

.btn-white,
.bg-white,
.badge-white,
.alert-white {
  background: #eef3fa;
  background: var(--light-color);
  color: #1b1b2f;
  color: var(--dark-color);
  border: #c1d3ed solid 1px;
}

.btn:hover {
  opacity: 0.8;
}

.bg-light,
.badge-light {
  border: #c1d3ed solid 1px;
}

.round-img {
  border-radius: 50%;
}

/* Forms */
input {
  margin: 1.2rem 0.3rem;
  font: inherit;
}

input:focus {
  outline: none;
}

.form-text {
  display: block;
  margin-top: 0.3rem;
  color: #c1d3ed;
}

input[type='text'],
input[type='email'],
input[type='password'],
input[type='date'],
select,
textarea {
  display: block;
  width: 100%;
  padding: 0.35rem 0.5rem;
  font-size: 1rem;
  border: 1px solid #c1d3ed;
}

input[type='submit'],
button {
  font: inherit;
  margin: 1.2rem 0.3rem;
}

table th,
table td {
  padding: 1rem;
  text-align: left;
}

table th {
  background: #eef3fa;
  background: var(--light-color);
}

/* Navbar */

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  vertical-align: middle;
  padding: 0.7rem 2rem;
  z-index: 1;
  width: 100%;
  opacity: 0.9;
  margin-bottom: 1rem;
}

.navbar ul {
  display: flex;
}

.navbar a {
  color: #eef3fa;
  color: var(--light-color);
  padding: 0.45rem;
  margin: 0 0.25rem;
}

.navbar a:hover {
  color: #eef3fa;
  color: var(--light-color);
  opacity: 0.8;
}

.navbar .welcome span {
  margin-right: 0.6rem;
}

/* Mobile Styles */
@media (max-width: 700px) {
  .hide-sm {
    display: none;
  }

  .grid-2,
  .grid-3,
  .grid-4 {
    grid-template-columns: 1fr;
  }

  /* Text Styles */
  .x-large {
    font-size: 3rem;
  }

  .large {
    font-size: 2rem;
  }

  .lead {
    font-size: 1rem;
  }

  /* Navbar */
  .navbar {
    display: block;
    text-align: center;
  }

  .navbar ul {
    text-align: center;
    justify-content: center;
  }
}

.bx {
  vertical-align: middle;
  margin: auto auto 0.15rem auto;
}

